.picture {
    margin-left: 80px;
    width: 510px;
    height: 340px;
    overflow: hidden;
    position: relative;
}

.picture img {
    margin-top: 15px;
    width: 510px;
    height: auto;
    border-radius: 5px;
    position: absolute;
    overflow: hidden;
    cursor: pointer;
}


