@import url("../../lib/keyframes.css");.nav {    width: 100vw;    height: 120px;    position: fixed;    z-index: 1000;    line-height: 100px;}.nav .home-button {    float: left;    z-index: 1050;    font-size: 18px;    font-weight: 300;    letter-spacing: -0.3px;    text-align: center;    color: #ffffff;    margin-left: 60px;}.nav .home-line {    position: absolute;    width: 100px;    height: 1px;    background-color: gray;    margin-top: 72px;    margin-left: 89px;}.nav .back-button {    float: right;    z-index: 1050;    font-size: 18px;    font-weight: 300;    letter-spacing: -0.3px;    text-align: center;    color: #ffffff;    margin-right: 81px;    -webkit-animation-name: soft-move-right-rev;    animation-name: soft-move-right-rev;    animation-duration: 0.5s;    -webkit-animation-duration: 0.5s;    animation-timing-function:linear;    animation-fill-mode:forwards;    animation-iteration-count: 1;    -webkit-animation-timing-function:linear;    -webkit-animation-fill-mode: forwards;    -webkit-animation-iteration-count: 1;}.nav .back-button:hover {    -webkit-animation-name: soft-move-right;    animation-name: soft-move-right;    animation-duration: 0.5s;    -webkit-animation-duration: 0.5s;    animation-timing-function:linear;    animation-fill-mode:forwards;    animation-iteration-count: 1;    -webkit-animation-timing-function:linear;    -webkit-animation-fill-mode: forwards;    -webkit-animation-iteration-count: 1;    cursor: pointer;}.nav .back-line {    right: 0;    position: absolute;    width: 100px;    height: 1px;    background-color: gray;    margin-top: 72px;}.nav .circle-group {    position: absolute;    display: table;    top: 0;    left: 0;    width: 100%;    height: 100%;}.nav .circle-group .inner-group {    display: table-cell;    text-align: center;    vertical-align: middle;    z-index: 1010;}.fadeIn {    -webkit-animation-name: fadeIn;    animation-name: fadeIn;    animation-duration: 0.2s;    -webkit-animation-duration: 0.2s;    animation-timing-function:linear;    animation-fill-mode:forwards;    animation-iteration-count: 1;    -webkit-animation-timing-function:linear;    -webkit-animation-fill-mode: forwards;    -webkit-animation-iteration-count: 1;}.fadeOut {    -webkit-animation-name: fadeOut;    animation-name: fadeOut;    animation-duration: 0.2s;    -webkit-animation-duration: 0.2s;    animation-timing-function:linear;    animation-fill-mode:forwards;    animation-iteration-count: 1;    -webkit-animation-timing-function:linear;    -webkit-animation-fill-mode: forwards;    -webkit-animation-iteration-count: 1;}.nav .circle-group .inner-group .content {    display: inline-block;    z-index: 1020;}.nav .circle-group .inner-group .content .circle {    width: 14px;    height: 14px;    border-radius: 14px;    border: 1px solid gray;    margin: 10px;    opacity: 0;    z-index: 1030;}.nav .circle-group .inner-group .content .circle-full {    width: 14px;    height: 14px;    border-radius: 14px;    border: 1px solid gray;    background-color: white;    margin: 10px;    z-index: 1030;}


